import React, { Fragment, Children } from 'react';
import { AppContextInterface, AppContextProvider } from '../AppContext';

interface DataProps {
    children: any
}

interface DataState {
  isLoaded: boolean; 
  items: any,
  error: {message : ''},
  totalcases: number,
  statewise: any
};

export default class DataProvider extends React.Component<DataProps, DataState> {
    state: DataState = {
        isLoaded: false,
        items: [],
        error: {message : ''},
        totalcases: 0,
        statewise: []
    };

    constructor(props:DataProps) {
      super(props);
      this.state = {
        error: {message : ''},
        isLoaded: false,
        items: [],
        totalcases: 0,
        statewise: []
      };
    }
  
    componentDidMount() {
      fetch("https://api.covid19india.org/states_daily.json")
        .then(res => res.json())
        .then(
          (result) => {
            let statewise = new Array;
            let statesarray = new Array;
            let statesdata = result.states_daily;
            let countrysum = 0;
            for (let i =0; i<statesdata.length;i++){ 
              if(statesdata[i]["status"] == 'Confirmed'){
                countrysum = countrysum + parseInt(statesdata[i]['tt']);
               statesarray.push(
                  {'total': countrysum, 'date': statesdata[i]["date"]}
                 )
                statewise.push({
                   "count": parseInt(statesdata[i]["tt"]), "date": statesdata[i]["date"]
                })
               }
            }

            this.setState({
              isLoaded: true,
              items: statesarray,
              totalcases: countrysum,
              statewise: statewise
            });
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            this.setState({
              isLoaded: true,
              error
            });
          }
        )
    }
  
    render() {
      const { error, isLoaded, items, totalcases, statewise } = this.state;
      debugger;
      const chartAppContext: AppContextInterface = {
        data: items,
        total: totalcases,
        statewise: statewise
      };
      if (error.message != '') {
        return <div>Error: {error.message}</div>;
      } else if (!isLoaded) {
        return <div>Loading...</div>;
      } else {
        return (
            <AppContextProvider value={chartAppContext}>
                <Fragment>
                    {this.props.children}
                </Fragment>
            </AppContextProvider>
        );
      }
    }
  }